.left-section {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  padding: 20px;
  padding-top: 40px;
  padding-left: 46px;
  background: url(/Multi-step-form/static/media/bg-sidebar-desktop.c92d1f80.svg);
  max-height: 568px;
  border-radius: 6px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  min-width: 270px;
}

.steps-wrapper {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.number-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid white;
  color: var(--colorn5);
  font-weight: 700;
}

.number-circle.active {
  background-color: var(--color4);
  color: var(--color1);
  border: none;
}

.step-text p {
  text-transform: uppercase;
  color: var(--colorn5);
  font-weight: 500;
  letter-spacing: 0.5px;
}

.step-text :first-child {
  color: var(--colorn1);
  margin-bottom: 2px;
  font-weight: 200;
}

.right-section {
  padding: 20px;
  justify-self: center;
  margin-top: 20px;
  min-width: 480px;
  max-width: 500px;
  position: relative;
}

.right-section > h1 {
  color: var(--color1);
  font-size: 38px;
  margin-bottom: 10px;
}

.right-section > p,
.duration-container p {
  color: var(--colorn1);
}

.duration-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  background-color: var(--colorn3);
  grid-column: 1 / -1;
  padding: 14px;
  align-items: center;
  justify-content: center;
}

.duration-container p.active {
  color: var(--color1);
}

.summary-container {
  margin-top: 40px;
  padding: 30px 22px;
  background-color: var(--colorn3);
  border-radius: 5px;
}

.plan-summary,
.addOns-summary,
.total-price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.addOns-summary p {
  margin-top: 20px;
}

.addOns-summary p {
  color: var(--color1);
}

.addOns-summary > :first-child,
.total-price > :first-child {
  color: var(--colorn1);
}

.plan-summary {
  grid-gap: 20px;
  gap: 20px;
  padding-bottom: 30px;
  border-bottom: 1px solid var(--colorn2);
}

.primary-bold {
  color: var(--color1);
  font-weight: 500;
}

.change-link {
  margin-top: 4px;
  color: var(--color2);
}

.change-link:hover {
  color: var(--color2);
  cursor: pointer;
  text-decoration: underline;
}

.total-price {
  padding: 0px 22px;
  margin-top: 20px;
}

.total-price :not(:first-child) {
  font-size: 20px;
  font-weight: 500;
  color: var(--color2);
}

.confirm-section {
  align-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  grid-gap: 10px;
  gap: 10px;
}

.confirm-section > img {
  margin-bottom: 30px;
}

@media (max-width: 890px) {
  .right-section {
    padding-bottom: 100px;
  }
}

@media (max-width: 820px) {
  .step-text {
    display: none;
  }
  .left-section {
    flex-direction: row;
    background-image: url(/Multi-step-form/static/media/bg-sidebar-mobile.e02fa37c.svg);
    min-width: 0;
    padding: 0px;
    padding-bottom: 60px;
    min-height: 170px;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
    border-radius: 4px;
  }
  .right-section {
    min-width: 0px;
    background-color: var(--colorn5);
    margin: 20px;
    padding: 30px;
    margin-top: 0px;
    margin-top: -60px;
    border-radius: 6px;
    padding-bottom: 100px;
  }
  .right-section h1 {
    font-size: 30px;
  }
}

form {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  gap: 20px;
  margin-top: 40px;
}

form > div {
  display: grid;
  grid-gap: 6px;
  gap: 6px;
}

label {
  color: var(--color1);
}

input {
  padding: 14px;
  border: 1px solid var(--colorn2);
  border-radius: 5px;
  font-weight: 500;
  color: var(--color1);
}

input:focus {
  border: 1px solid transparent;
  outline: 1px solid var(--color1);
}

.error-message {
  color: var(--color5);
  justify-self: end;
  margin-bottom: -14px;
}

.buttons-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  bottom: 20px;
}

.buttons-container.end {
  justify-content: end;
}

.back-btn {
  font-weight: 500;
  color: var(--colorn1);
  cursor: pointer;
}

.next-btn {
  padding: 14px 22px;
  border-radius: 6px;
  background-color: var(--color1);
  color: var(--colorn5);
  border: none;
  font-weight: 700;
  letter-spacing: 0.5px;
  font-family: "Ubuntu", sans-serif;
  outline: none;
}

.next-btn:hover {
  cursor: pointer;
  background-color: var(--color2);
}

.back-btn:hover {
  cursor: pointer;
  color: var(--color1);
}

@media (max-width: 890px) {
  .buttons-container {
    width: calc(100% - 60px);
  }
}

input[type="radio"] {
  display: none;
}

.radio-form {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
}

.radio-container {
  padding: 20px;
  padding-left: 16px;
  padding-right: 30px;
  border: 1px solid var(--colorn1);
  border-radius: 6px;
}

.radio-container:hover {
  cursor: pointer;
  border: 1px solid var(--color2);
}

.radio-container.active {
  border: 1px solid var(--color2);
  background: var(--colorn4);
}

.radio-text {
  margin-top: 40px;
}

.radio-text > h1 {
  font-size: 16px;
  font-weight: 500;
}

.radio-text > p {
  margin-top: 4px;
  color: var(--colorn1);
}

#primary {
  color: var(--color1);
  font-size: 14px;
}

@media (max-width: 890px) {
  .radio-form {
    grid-template-columns: 1fr;
  }

  .radio-container {
    grid-gap: 14px;
    gap: 14px;
    display: flex;
    align-items: center;
  }

  .radio-text {
    margin-top: 0px;
  }
}

input[type="checkbox"] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;
}

.toggle {
  cursor: pointer;
  width: 47px;
  height: 22px;
  border-radius: 100px;
  background: var(--color1);
  text-indent: -9999px;
  float: right;
  position: relative;
}

.toggle::after {
  content: "";
  position: absolute;
  background-color: var(--colorn5);
  width: 14px;
  height: 14px;
  border-radius: 100px;
  top: 4px;
  left: 3px;
  transition: 0.3s;
}

input:checked + .toggle {
  background-color: var(--color2);
}

input:checked + .toggle::after {
  -webkit-transform: translateX(26px);
          transform: translateX(26px);
}

.custom-checkbox {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: var(--colorn5);
  border: 1px solid var(--colorn2);
  border-radius: 4px;
  margin-right: 5px;
  position: relative;
}

input[type="checkbox"]:checked + .custom-checkbox {
  background-color: var(--color2);
  border-color: var(--color2);
}

.custom-checkbox::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
  width: 4px;
  height: 9px;
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  opacity: 0;
}

input[type="checkbox"]:checked + .custom-checkbox::before {
  opacity: 1;
}

.addons-wrapper {
  display: grid;
  grid-template-columns: 30px 1fr 60px;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
  background-color: var(--colorn4);
  padding: 16px;
  border: 1px solid var(--colorn2);
  margin-top: 20px;
  border-radius: 5px;
}

.addons-wrapper.checked {
  border: 1px solid var(--color2);
}

.option-text > h1 {
  font-size: 16px;
  color: var(--color1);
  margin-bottom: 4px;
}

.option-text > p {
  color: var(--colorn1);
}

.addons-price {
  color: var(--color2);
}

@media (max-width: 500px) {
  .addons-wrapper {
    grid-gap: 2px;
    gap: 2px;
    grid-template-columns: 30px 1fr auto;
    font-size: 14px;
    padding: 12px 8px;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

:root {
  --color1: hsl(213, 96%, 18%);
  --color2: hsl(243, 100%, 62%);
  --color3: hsl(228, 100%, 84%);
  --color4: hsl(206, 94%, 87%);
  --color5: hsl(354, 84%, 57%);

  --colorn1: hsl(231, 11%, 63%);
  --colorn2: hsl(229, 24%, 87%);
  --colorn3: hsl(217, 100%, 97%);
  --colorn4: hsl(231, 100%, 99%);
  --colorn5: hsl(0, 0%, 100%);
}

main {
  background-color: hsl(217, 100%, 97%);
  background-color: var(--colorn3);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  font-size: 1rem;
}

.main-container {
  background-color: hsl(0, 0%, 100%);
  background-color: var(--colorn5);
  display: grid;
  grid-template-columns: 274px minmax(420px, 726px);
  padding: 20px;
  border-radius: 5px;
  margin: 20px;
  min-height: 608px;
}

@media (max-width: 940px) {
  .main-container {
    grid-gap: 20px;
    gap: 20px;
  }
}

@media (max-width: 820px) {
  main {
    align-items: start;
  }
  .main-container {
    background-color: hsl(217, 100%, 97%);
    background-color: var(--colorn3);
    grid-template-columns: 1fr;
    margin: 0px;
    padding: 0px;
    grid-gap: 0px;
    gap: 0px;
  }
}

